/**
 *
 * Component to attach files to the storage
 * Author : Daniel Contreras Aladro
 * Date : 2017-02-21
 *
 */
declare var JSZipUtils: any;

import {
  Component,
  OnInit, OnDestroy,
  AfterViewInit,
  Input,
  Output,
  EventEmitter,
  ElementRef,
  ViewChild,
  trigger,
  state,
  style,
  transition,
  animate,
  keyframes } from '@angular/core';

import { environment } from '../../../environments/environment';

import tinymce from 'tinymce/tinymce';
import 'tinymce/themes/modern/theme';

// Plugins
import 'tinymce/plugins/paste/plugin';
import 'tinymce/plugins/searchreplace/plugin';

import { TranslateService } from '@ngx-translate/core';

import * as $ from 'jquery/dist/jquery';

import { ZendeskService } from '../../services/zendesk.service';
import { PayaraService } from '../../services/payara.service';

import { Attachment } from '../../classes/attachment';

@Component({
  selector: 'app-add-file',
  templateUrl: './add-file.component.html',
  styleUrls: ['./add-file.component.css'],
    animations: [

        trigger('state', [
            state('no', style({
              backgroundColor: 'transparent',
              color: '#002c3e'
            })),
            state('loading', style({
              backgroundColor: '#f0981b',
              color: '#ffffff'
            })),
            state('yes', style({
              backgroundColor: '#47B404',
              color: '#E6E6E6'
            })),
            transition('loading => yes',
              [
                animate(300,
                  keyframes([
                    style({transform: 'scale(1)', offset: 0}),
                    style({transform: 'scale(1.025)',offset: 0.1}),
                    style({transform: 'scale(1.05)',offset: 0.2}),
                    style({transform: 'scale(1.075)',offset: 0.3}),
                    style({transform: 'scale(1.1)',offset: 0.4}),
                    style({transform: 'scale(1.125)',offset: 0.5}),
                    style({transform: 'scale(1.1)',offset: 0.6}),
                    style({transform: 'scale(1.075)',offset: 0.7}),
                    style({transform: 'scale(1.05)',offset: 0.8}),
                    style({transform: 'scale(1.025)',offset: 0.9}),
                  ])
                )
              ]
            ),
            transition('no => loading',
              [
                animate(900,
                  keyframes([
                    style({transform: 'scale(1)', offset: 0}),
                    style({transform: 'scale(1.025)',offset: 0.1}),
                    style({transform: 'scale(1.05)',offset: 0.2}),
                    style({transform: 'scale(1.075)',offset: 0.3}),
                    style({transform: 'scale(1.1)',offset: 0.4}),
                    style({transform: 'scale(1.125)',offset: 0.5}),
                    style({transform: 'scale(1.1)',offset: 0.6}),
                    style({transform: 'scale(1.075)',offset: 0.7}),
                    style({transform: 'scale(1.05)',offset: 0.8}),
                    style({transform: 'scale(1.025)',offset: 0.9}),
                  ])
                )
              ]
            )
        ]),

    ]
})
export class AddFileComponent implements OnInit,AfterViewInit, OnDestroy {
  /**
   * Properties and objects of the component
   */
  environment = environment;
  tinymce = tinymce;

  staticFileButtons:any = [
    {
      title:'Monitoring Data',
      url:'get-monitoring-configuration.json',
      loaded:'no',
      type:'json'
    },
    {
      title:'Domain Log',
      url:'view-log',
      loaded:'no',
      type:'log'
    },
  ];

  fileButtons:any = [];

  filesLoaded:boolean = false;
  filesSaved:boolean = false;

  @Output() saved = new EventEmitter<Attachment>();

  errorMessage: string;
  loadingMessage: string;
  loadingMessageXml: string;

  elementId:string = 'domainEditor';

  editor;
  /**
   * ngAfterViewInit - AfterViewInit method of the component
   */
  ngAfterViewInit() {
    let initObject = {
      relative_urls : false,
      remove_script_host : false,
      document_base_url : null,
      skin_url: '../assets/skins/lightgray',
      selector: '#' + this.elementId,
      plugins: ['paste','searchreplace'],
      elementpath: false,
      height : 300,
      menubar: false,
      toolbar:false,
      statusbar: false,
      forced_root_block : false,
      setup: editor => {this.editor = editor;},
    };
    if(this.environment.production){
      initObject.relative_urls = false;
      initObject.remove_script_host = false;
      initObject.document_base_url = 'http://localhost:'+window["globalPort"]+'/resource/payara_support/zendesk/';
      initObject.skin_url = './assets/skins/lightgray';
    }

    this.tinymce.init(initObject);
    this.discardXml();
  }
  /**
   * constructor - Constructor of the component
   */
  constructor(
    private translate: TranslateService,
    private zendeskService: ZendeskService,
    private payaraService: PayaraService
  ) {
  }
  /**
   * ngOnDestroy - OnDestroy method of the component
   */
  ngOnDestroy() {
    $('.ng-tool-tip-content').hide();
  }
  /**
   * ngOnInit - OnInit method of the component
   */
  ngOnInit() {
    this.fileButtons = Object.assign([], this.staticFileButtons);
    this.payaraService.getServerInstances()
    .then(
      responseData => {
        if (responseData !== undefined && responseData !== null) {
          this.filesLoaded = true;
          let responseArray = Object.keys(responseData);
          for (let prop in responseArray) {
            this.fileButtons.push({
              title: this.translate.instant('JVM Report')+ ': ' +responseArray[prop],
              url:'servers/server/'+responseArray[prop]+'/generate-jvm-report.json?type=summary',
              loaded:'no',
              type:'json'
            });
            this.fileButtons.push({
              title: this.translate.instant('Thread Dump')+ ': ' +responseArray[prop],
              url:'servers/server/'+responseArray[prop]+'/generate-jvm-report.json?type=thread',
              loaded:'no',
              type:'json'
            });
            this.fileButtons.push({
              title: 'Log: ' +responseArray[prop],
              url:'logs_'+responseArray[prop]+'.zip?contentSourceId=LogFiles&target='+responseArray[prop]+'&restUrl=http://localhost:4848/management/domain',
              file:'logs_'+responseArray[prop]+'.zip',
              loaded:'no',
              type:'zip'
            });
          }
        }
      },
      error => {
        this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
            this.errorMessage = res;
        });
      }
    );
  }
  /**
  * searchFile - Event to get the data for the file when the button is pressed
  *
  * @param {any}  fileButton Object with the data of the fileButton
  */
  searchFile(fileButton:any) {
    fileButton.loaded = 'loading';
    this.loadingMessage = "Loading file ...";
    if(fileButton.url && fileButton.url!==''){
      switch(fileButton.type) {
          case 'log':
            this.getFile(fileButton.url,false,(response) => {
              this.addFile(
                JSON.stringify(response['_body']),
                fileButton.title.replace(' ','_')+'.log',
                'application/octet-stream',
                (response) => {
                  this.saved.emit(response);
                  fileButton.loaded = 'yes';
                  this.filesSaved = true;
                  this.loadingMessage = null;
                }
              );
            });
            break;
        case 'json':
          this.getFile(fileButton.url,false,(response) => {
            this.addFile(
              JSON.stringify(response.json().message),
              fileButton.title.replace(' ','_')+'.txt',
              'application/octet-stream',
              (response) => {
                this.saved.emit(response);
                fileButton.loaded = 'yes';
                this.filesSaved = true;
                this.loadingMessage = null;
              }
            );
          });
          break;
        case 'zip':
          this.getFile(fileButton.url,true,(response2) => {
              this.addFile(
                new File([response2], fileButton.file, {type:'application/zip'}),
                fileButton.file,
                'application/zip',
                (result) => {
                  this.saved.emit(result);
                  fileButton.loaded = 'yes';
                  this.filesSaved = true;
                  this.loadingMessage = null;
                  }
              );
          });
          break;
      }
    }
  }
  /**
  * cleanFiles - Method to clean the add file buttons and enable it
  */
  cleanFiles(){
    this.filesSaved = false;
    this.filesLoaded = false;
    for (let button of this.fileButtons) {
        button.loaded = 'no'
    }
  }
  /**
  * searchFile - Event to get the data for the file when the button is pressed
  *
  * @param {any}      content Object with file's data
  * @param {string}   name String with file's name
  * @param {string}   type String with file's mimetype
  * @param {any}      back Callback function to return sync value
  */
  addFile(content:any,name:string,type:string,back:any){
    this.zendeskService.addNewFile(content, name,type)
    .then(
      responseData => {
        if (responseData !== undefined && responseData !== null) {
          back(JSON.parse(responseData).upload);
        }
      },
      error => {
        this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
            this.errorMessage = res;
        });
      }
    );
  }
  /**
  * searchFile - Event to get the data for the file when the button is pressed
  *
  * @param {string}   url String with the endpoint to call
  * @param {boolean}  parse Boolean to know if parse data is needed
  * @param {any}      back Callback function to return sync value
  */
  getFile(url:string,parse:boolean,back:any){
    if(parse){
      JSZipUtils.getBinaryContent(
        this.payaraService.connectionData.filesURL + url,
        (err,data) => {
          if(err) {
            this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
                this.errorMessage = res;
            });
          }
          back(data);
        }
      );
    }else{
      this.payaraService.getFile(url)
      .then(
        responseData => {
          if (responseData !== undefined && responseData !== null) {
            back(responseData);
          }
        },
        error => {
          this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
              this.errorMessage = res;
          });
        }
      );
    }
  }
  /**
   * discardXml - Function to discard changes made to xml file shown
   */
  discardXml(){
    this.getFile('configs/config/server-config/zendesk-support-configuration/get-domain-xml.json',false,(response) => {
        if (response !== undefined && response !== null) {
          this.tinymce.activeEditor.setContent(response.json().message.replace(/>/g,'&gt;').replace(/</g,'<br>&lt;').replace('<br>',''));
        }
    });
  }
  /**
   * saveXml - Function to save changes made to xml file shown
   */
  saveXml(){
    this.loadingMessageXml = "Loading domain.xml file ...";
    this.addFile(
      this.editor.getContent().replace(/&gt;/g,'>').replace(/&lt;/g,'<').replace(/<br \/>/g,'\n'),
      'Domain.xml',
      'application/binary',
      (response) => {
        this.saved.emit(response);
        this.filesSaved = true;
        this.tinymce.activeEditor.setMode('readonly');
        this.loadingMessageXml = null;
      }
    );
  }

}



